import { createComponentFromModel } from 'core/loadEntities';

export default function (entity) {
  const forces = {
    name: 'forces',
    scope: 'common',
    params: {
      parts: {
        removeDiscover: {
          x: 0,
          y: 0,
          z: 0,
          rotateX: 0,
          rotateY: 0,
          duration: 1000,
          easing: {
            scope: 'ease',
            name: 'easeLinear',
          },
          ending: {
            scope: 'aJam',
            name: 'ennemyRemoveDiscover',
          },
          elapsed: 0,
        },
      },
    },
  };
  const newForces = createComponentFromModel.call(this, forces);

  const entityForces = entity.get('forces');


entityForces.parts.removeDiscover=newForces.parts.removeDiscover
}
